import Layout from '@/layout/Default'

export default {
  path: '/in-storage',
  component: Layout,
  name: 'InStorageModule',
  redirect: '/in-storage/purchase',
  meta: {
    icon: 'login',
    title: '入库管理'
  },
  children: [
    // 采购订单 相关页面
    {
      path: 'purchase',
      name: 'InStoragePurchase',
      component: () => import(/* webpackChunkName: "InStoragePurchase" */'@/views/in-storage/purchase'),
      meta: {
        icon: 'alipay',
        title: '采购订单'
      }
    },
    // Tip 采购订单交接单列表(id: 订单id;type: 订单类型)
    // Tip 订单类型，1：采购订单，2：外协订单，3：寄售订单，4：成品订单
    {
      path: 'purchase-delivery/:id/:type',
      name: 'InStoragePurchaseDelivery',
      component: () => import(/* webpackChunkName: "InStoragePurchaseDelivery" */'@/views/in-storage/purchase/delivery'),
      meta: {
        icon: 'alipay',
        title: '采购订单-交接单'
      }
    },
    // Tip 新增交接单(view: 页面类型;orderId: 订单id;type: 订单类型)
    // Tip 页面类型，add：新增，modify：修改，detail：详情
    // Tip 订单类型，1：采购订单，2：外协订单，3：寄售订单，4：成品订单
    {
      path: 'delivery-add/:view/:orderId/:type/:routeName',
      name: 'InStorageDeliveryAdd',
      component: () => import(/* webpackChunkName: "InStorageDeliveryAdd" */'@/views/in-storage/purchase/delivery/add'),
      meta: {
        icon: 'alipay',
        title: '新增采购交接单'
      }
    },
    // Tip 交接单修改(view: 页面类型;orderId: 订单id;id: 交接单id;type: 订单类型)
    // Tip 页面类型，add：新增，modify：修改，detail：详情
    // Tip 订单类型，1：采购订单，2：外协订单，3：寄售订单，4：成品订单
    {
      path: 'delivery-modify/:view/:orderId/:id/:type/:routeName',
      name: 'InStorageDeliveryModify',
      component: () => import(/* webpackChunkName: "InStorageDeliveryModify" */'@/views/in-storage/purchase/delivery/add'),
      meta: {
        icon: 'alipay',
        title: '采购交接单修改'
      }
    },
    // Tip 交接单详情(view: 页面类型;orderId: 订单id;id: 交接单id;type: 订单类型)
    // Tip 页面类型，add：新增，modify：修改，detail：详情
    // Tip 订单类型，1：采购订单，2：外协订单，3：寄售订单，4：成品订单
    {
      path: 'delivery-detail/:view/:orderId/:id/:type/:routeName',
      name: 'InStorageDeliveryDetail',
      component: () => import(/* webpackChunkName: "InStorageDeliveryDetail" */'@/views/in-storage/purchase/delivery/add'),
      meta: {
        icon: 'alipay',
        title: '采购交接单详情'
      }
    },
    {
      path: 'purchase-document/:id',
      name: 'InStoragePurchaseDocument',
      component: () => import(/* webpackChunkName: "InStoragePurchaseDocument" */'@/views/in-storage/purchase/document'),
      meta: {
        icon: 'alipay',
        title: '采购订单-文档管理'
      }
    },
    // 物资检验 相关页面
    {
      path: 'inspection',
      name: 'InStorageInspection',
      component: () => import(/* webpackChunkName: "InStorageInspection" */'@/views/in-storage/inspection'),
      meta: {
        icon: 'bell',
        title: '物资检验'
      }
    },
    {
      path: 'inspection-detail/:id',
      name: 'InStorageInspectionDetail',
      component: () => import(/* webpackChunkName: "InStorageInspectionDetail" */'@/views/in-storage/inspection/detail'),
      meta: {
        icon: 'bell',
        title: '物资检验详情'
      }
    },
    {
      path: 'inspection-start/:id',
      name: 'InStorageInspectionStart',
      component: () => import(/* webpackChunkName: "InStorageInspectionStart" */'@/views/in-storage/inspection/startInspection'),
      meta: {
        icon: 'eye',
        title: '物资开始检验'
      }
    },
    {
      path: 'inspection-document/:id',
      name: 'InStorageInspectionDocument',
      component: () => import(/* webpackChunkName: "InStorageInspectionDocument" */'@/views/in-storage/inspection/document'),
      meta: {
        icon: '',
        title: '物资检验-文档管理'
      }
    },
    // 入库信息相关页面
    {
      path: 'info',
      name: 'InStorageInfo',
      component: () => import(/* webpackChunkName: "InStorageInfo" */'@/views/in-storage/info'),
      meta: {
        icon: 'codeConsole',
        title: '入库信息'
      }
    },
    {
      path: 'info-detail/:id',
      name: 'InStorageInfoDetail',
      component: () => import(/* webpackChunkName: "InStorageInfoDetail" */'@/views/in-storage/info/warehousingEntryOrder'),
      props: route => ({ orderId: route.params.id }),
      meta: {
        icon: 'codeConsole',
        title: '入库信息详情'
      }
    },
    {
      path: 'info-add/:id', // 交接单id
      name: 'InStorageInfoAdd',
      component: () => import(/* webpackChunkName: "InStorageInfoAdd" */'@/views/in-storage/info/warehousingEntryOrderAdd'),
      props: route => ({ deliveryReceiptId: route.params.id }),
      meta: {
        icon: 'codeConsole',
        title: '新增入库信息'
      }
    },
    {
      path: 'info-receipt/:id', // 入库单id
      // path: 'info-receipt/:orderId/:ids',
      name: 'InStorageInfoReceipt',
      component: () => import(/* webpackChunkName: "InStorageInfoReceipt" */'@/views/in-storage/info/receipt'),
      props: route => ({ id: route.params.id }),
      meta: {
        icon: 'codeConsole',
        title: '入库单据信息'
      }
    },
    // {
    //   path: 'info-receipt-preview/:orderId/:ids',
    //   name: 'InStorageInfoReceiptPreview',
    //   component: () => import(/* webpackChunkName: "InStorageInfoReceiptPreview" */'@/views/in-storage/info/receipt-preview'),
    //   meta: {
    //     icon: 'codeConsole',
    //     title: '打印预览'
    //   }
    // },
    {
      path: 'info-delivery/:orderId/:id',
      name: 'InStorageInfoDelivery',
      component: () => import(/* webpackChunkName: "InStorageInfoDelivery" */'@/views/in-storage/info/delivery'),
      meta: {
        icon: 'codeConsole',
        title: '入库单信息'
      }
    },
    // Tip 物资寄售相关页面
    {
      path: 'consign',
      name: 'InStorageConsign',
      component: () => import(/* webpackChunkName: "InStorageConsign" */'@/views/in-storage/consign'),
      meta: {
        icon: 'Steve-Jobs',
        title: '物资寄售'
      }
    },
    {
      path: 'consign-delivery-add/:view/:orderId/:type/:routeName',
      name: 'InStorageConsignDeliveryAdd',
      component: () => import(/* webpackChunkName: "InStorageConsignDeliveryAdd" */'@/views/in-storage/consign/delivery/add'),
      meta: {
        icon: 'alipay',
        title: '新增寄售交接单'
      }
    },
    {
      path: 'consign-delivery-modify/:view/:orderId/:id/:type/:routeName',
      name: 'InStorageConsignDeliveryModify',
      component: () => import(/* webpackChunkName: "InStorageConsignDeliveryModify" */'@/views/in-storage/consign/delivery/edit'),
      meta: {
        icon: 'alipay',
        title: '寄售交接单修改'
      }
    },
    {
      path: 'consign-delivery-detail/:view/:orderId/:id/:type/:routeName',
      name: 'InStorageConsignDeliveryDetail',
      component: () => import(/* webpackChunkName: "InStorageConsignDeliveryDetail" */'@/views/in-storage/consign/delivery/detail'),
      meta: {
        icon: 'alipay',
        title: '寄售交接单详情'
      }
    },
    // Tip 物资寄售交接单列表(id: 订单id;type: 订单类型)
    // Tip 订单类型，1：采购订单，2：外协订单，3：寄售订单，4：成品订单
    {
      path: 'consign-delivery/:id/:type',
      name: 'InStorageConsignDelivery',
      component: () => import(/* webpackChunkName: "InStorageConsignDelivery" */'@/views/in-storage/consign/delivery'),
      meta: {
        icon: 'alipay',
        title: '物资寄售-交接单'
      }
    },
    // Tip 外协入库相关页面
    {
      path: 'assist',
      name: 'InStorageAssist',
      component: () => import(/* webpackChunkName: "InStorageAssist" */'@/views/in-storage/assist'),
      meta: {
        icon: 'backup',
        title: '外协入库'
      }
    },
    // Tip 外协入库交接单列表(id: 订单id;type: 订单类型)
    // Tip 订单类型，1：采购订单，2：外协订单，3：寄售订单，4：成品订单
    {
      path: 'assist-delivery/:id/:type',
      name: 'InStorageAssistDelivery',
      component: () => import(/* webpackChunkName: "InStorageAssistDelivery" */'@/views/in-storage/assist/delivery'),
      meta: {
        icon: 'alipay',
        title: '外协入库-交接单'
      }
    },
    // 成品入库相关页面
    {
      path: 'finish',
      name: 'InStorageFinish',
      component: () => import(/* webpackChunkName: "InStorageFinish" */'@/views/in-storage/finish'),
      meta: {
        icon: 'delivery',
        title: '成品入库'
      }
    },
    // Tip 成品入库交接单列表(id: 订单id;type: 订单类型)
    // Tip 订单类型，1：采购订单，2：外协订单，3：寄售订单，4：成品订单
    {
      path: 'finish-delivery/:id/:type',
      name: 'InStorageFinishDelivery',
      component: () => import(/* webpackChunkName: "InStorageFinishDelivery" */'@/views/in-storage/purchase/delivery'),
      meta: {
        icon: 'alipay',
        title: '成品入库-交接单'
      }
    },
    // 上架管理相关页面
    {
      path: 'grounding',
      name: 'InStorageGrounding',
      component: () => import(/* webpackChunkName: "InStorageGrounding" */'@/views/in-storage/grounding'),
      meta: {
        icon: 'deploy',
        title: '上架管理'
      }
    },
    {
      path: 'materialSupplier',
      name: 'InStorageMaterialSupplier',
      component: () => import(/* webpackChunkName: "InStorageMaterialSupplier" */'@/views/in-storage/materialSupplier'),
      meta: {
        icon: 'alipay',
        title: '物料供应商'
      }
    }
  ]
}
