<template>
  <el-dialog
    :title="title"
    top="5vh"
    :visible="value"
    :show-close="true"
    width="900px"
    :lock-scroll="false"
    :destroy-on-close="true"
    :append-to-body="true"
    :close-on-click-modal="false"
    :close-on-press-escape="false"
    :before-close="beforeClose"
    @open="initSelect"
  >
    <el-card>
      <template v-slot:header>
        <div class="clearfix">
          <span class="card-title fl">待办 ({{ logsData.length }})</span>
          <el-link
            type="info"
            class="computed fr"
            @click="toList"
          >历史查询</el-link>
        </div>
      </template>
      <el-scrollbar class="query-more-scroll-drawer">
        <!-- 列表 -->
        <div v-if="!!logsData.length" v-loading="loading" class="logArea">
          <div v-for="(item, index) in logsData" :key="index" class="item">
            <el-divider v-if="index" />
            <div class="clearfix">
              <p class="timeArea fl">
                <span class="title">日期：</span>
                <span class="title time">{{ item.startTime }}</span>
              </p>
              <el-link
                type="primary"
                class="fr"
                :disabled="routeQueryTaskId === item.taskId"
                @click="handleDealWith(item)"
              >办理</el-link>
            </div>

            <div class="logContent">
              <p class="title">待办事项：</p>
              <ul class="logUl">
                <li>{{ item.formName }} ({{ item.taskName }})</li>
              </ul>
            </div>
          </div>
        </div>
        <div v-else v-loading="loading" class="noData">暂无待办</div>
      </el-scrollbar>
    </el-card>
  </el-dialog>
</template>

<script>
import { getMyTaskApi, getNodeInfoApi } from '@/apis/modules/common/task'
import moment from 'moment'
moment.locale('zh-cn')
export default {
  name: 'TodoList',
  mixins: [],
  props: {
    value: {
      required: true,
      type: Boolean
    },
    title: {
      type: String,
      default: 'TODO'
    },
    extraInfo: {
      type: Object,
      default: () => {}
    }
  },
  dicts: [],
  data() {
    return {
      loading: false,
      logsData: []
    }
  },
  computed: {
    routeQueryTaskId() {
      const { query: { taskId }} = this.$route
      return taskId
    }
  },
  methods: {
    // Tip 弹窗打开加载
    initSelect() {
      this.init()
    },
    // Tip 初始化
    init() {
      this.loading = true
      return new Promise((resolve, reject) => {
        getMyTaskApi({})
          .then((res) => {
            const { code, data } = res
            if (!code) {
              this.logsData = data.map(item => {
                item.startTime = moment(item.startTime || new Date()).format('YYYY-MM-DD')
                return item
              })
              this.$emit('sendLength', data.length)
            }
            this.loading = false
            resolve(res)
          })
          .catch((err) => {
            this.loading = false
            reject(err)
          })
      })
    },
    // Tip 办理
    handleDealWith(item) {
      const { businessKey, taskId, processInstanceId } = item
      const id = businessKey.split(':')[1] // 业务id
      const billNumber = businessKey.split(':')[0] // 单据编号
      if (!id) return
      const query = {
        billNumber,
        taskId,
        processInstanceId
      }
      getNodeInfoApi({
        taskId
      }).then((res) => {
        const { code, data } = res
        if (!code) {
          const { taskKey, step } = data
          taskKey.indexOf('audit') === -1 ? query.type = 'edit' : query.type = 'audit'
          this.beforeClose()
          this.$router.push({
            name: taskKey,
            params: {
              id // 业务ID
            },
            query: {
              ...query,
              step
            }
          })
        }
        this.loading = false
      }).catch((err) => {
        this.loading = false
        console.log(err)
      })
    },
    // Tip 历史查询
    toList() {
      this.beforeClose()
      this.$router.push({
        name: 'DashboardTaskList'
      })
    },
    // Tip 弹窗关闭
    beforeClose(done) {
      this.$emit('input', false)
      typeof done === 'function' && done()
    }
  }
}
</script>

<style lang="scss" scoped>
.query-more-scroll-drawer {
  width: 100%;
  height: 45vh;
}
::v-deep .el-scrollbar .el-scrollbar__wrap {
  overflow-x: hidden;
}
// ::v-deep .el-dialog__body {
//   padding-top: 10px;
//   padding-bottom: 0;
// }
// .query-more-drawer-wrapper {
//   min-height: 420px;
// }
// 弹窗底部操作区域样式
// .query-more-drawer-footer {
//   height: 44px;
//   border-top: 1px solid #F5F5F5;
// }
::v-deep.el-card > .el-card__body {
  padding: 10px 0px 16px !important;
}
.card-title {
  // font-size: 20px;
  font-weight: bolder;
}
.logArea {
  padding: 10px 16px;
  overflow: hidden;
  .item {
    .title {
      font-size: 13px;
    }
    .time {
      color: #87de75;
    }
    .logContent {
      .logUl {
        padding-left: 30px;
        li {
          font-size: 12px;
          list-style: disc;
          line-height: 20px;
        }
      }
    }
  }
}
.noData {
  text-align: center;
  line-height: 200px;
  font-weight: bolder;
  font-size: 20px;
  color: #909399;
}
</style>

