<template>
  <div :class="{'has-logo':showLogo}">
    <logo v-if="showLogo" :collapse="isCollapse" />
    <el-scrollbar wrap-class="scrollbar-wrapper menu-scrollbar">
      <!-- 正常情况
      菜单背景为深蓝
      活动的菜单文本颜色为蓝色
      菜单文本颜色为灰白色 -->
      <!-- 护眼模式
      菜单背景为 色
      活动的菜单文本颜色为 色
      菜单文本颜色为 色 -->
      <el-menu
        :default-active="activeMenu"
        :collapse="isCollapse"
        :background-color="variables.menuBg"
        :text-color="variables.menuText"
        :unique-opened="false"
        :active-text-color="variables.menuActiveText"
        :collapse-transition="false"
        mode="vertical"
      >
        <sidebar-item v-for="route in routes" :key="route.path" :item="route" :base-path="route.path" />
      </el-menu>
      <!-- 库位 -->
      <!-- <div class="location-menu-wrapper">
        <div class="location-menu-item">
          <svg-icon icon-class="bell" />
          <span>库位选择</span>
        </div>
      </div> -->
    </el-scrollbar>
  </div>
</template>

<script>
import { mapGetters } from 'vuex'
import Logo from './Logo'
import SidebarItem from './SidebarItem'
import variables from '@/styles/variable.scss'

export default {
  components: { SidebarItem, Logo },
  computed: {
    ...mapGetters({
      sidebar: 'app/sidebar',
      routes: 'routes'
    }),
    activeMenu() {
      const route = this.$route
      const { meta, path } = route
      // if set path, the sidebar will highlight the path you set
      if (meta.activeMenu) {
        return meta.activeMenu
      }
      return path
    },
    showLogo() {
      return this.$store.state.app.sidebarLogo
    },
    variables() {
      return variables
    },
    isCollapse() {
      return !this.sidebar.opened
    }
  }
}
</script>

<style lang="scss" scoped>
.menu-scrollbar {
  display: flex;
  flex-direction: row;
}
.location-menu-wrapper {
  color: #fff;
  line-height: 56px;
  font-size: 14px;
  align-self: flex-end;
}
</style>

