import Vue from 'vue'
import Es6Promise from 'es6-promise'
require('es6-promise').polyfill()
Es6Promise.polyfill()

import ElementUI from 'element-ui'
import 'element-ui/lib/theme-chalk/index.css'

// 数据字典
import dict from './directive/dict'

import App from './App.vue'
import store from './store'
import { cookieToToken } from '@/utils/auth'
cookieToToken()

import router from './routers'
// Tip 全局注册svg图
import './icons' // icon
// Tip 图片预览
import Viewer from 'v-viewer'
import 'viewerjs/dist/viewer.css'
// Tip 权限控制
import permission from '@/directive/permission'
// Tip 过滤器
import * as filters from './filters'
// Tip 实例属性
import * as prototypes from './utils/prototypes'
// Tip 错误日志收集
import './utils/error-log'
// Tip SVG图标
import SvgIcon from 'vue-svgicon-coms'
// Tip 大屏数据展示组件库
import dataV from '@jiaminghi/data-view' // 监控展示
// Tip 图表功能相关
import VueECharts from 'vue-echarts'
import './plugins/echarts/echarts.js'
// Tip 全局组件注册
import customCom from '@/components'

// Vue.use(VueContextMenu)
Vue.use(SvgIcon)
Vue.use(permission)
Vue.use(dict)
Vue.use(Viewer, {
  'zIndex': 9999
})
Vue.use(dataV)
Vue.use(ElementUI, {
  size: 'mini' // set element-ui default size
})
Vue.use(customCom)

// 过滤器注册
Object.keys(filters).forEach(key => {
  Vue.filter(key, filters[key])
})
// vue实例属性挂载
Object.keys(prototypes).forEach(key => {
  Vue.prototype['$' + key] = prototypes[key]
})

Vue.config.productionTip = false
// 全局注册可视化组件
Vue.component('v-chart', VueECharts)

// socket 连接参数
// const socketOptions = {
//   autoConnect: false, // 自动连接     这里为我项目需求  需要在指定情况下才连接socket
//   path: '/socket/433434'
// }

// // // 注册
// Vue.use(
//   new VueSocketIO({
//     debug: true, // debug调试，生产建议关闭
//     // connection: SocketIO('ws://127.0.0.1:1024', socketOptions)
//     connection: SocketIO('192.168.10.145:8988', socketOptions)
//     // store // 如果没有使用到store可以不用写
//   })
// )

new Vue({
  router,
  store,
  render: h => h(App)
}).$mount('#app')
