import Layout from '@/layout/Default'

export default [
  {
    path: '/',
    component: Layout,
    name: 'dashboard',
    redirect: '/dashboard',
    meta: {
      icon: 'dashboard',
      title: 'dashboard'
    },
    children: [
      {
        path: 'dashboard',
        name: 'WmsDashboard',
        component: () => import(/* webpackChunkName: "WmsDashboard" */'@/views/dashboard'),
        meta: {
          icon: 'dashboard',
          title: '首页'
        }
      },
      {
        path: 'task-list',
        name: 'DashboardTaskList',
        hidden: true,
        component: () => import(/* webpackChunkName: "DashboardTaskList" */'@/views/dashboard/task-list'),
        meta: {
          icon: 'dashboard',
          title: '已办理任务'
        }
      },
      {
        path: 'message',
        name: 'DashboardMessage',
        hidden: true,
        component: () => import(/* webpackChunkName: "DashboardMessage" */'@/views/dashboard/message'),
        meta: {
          icon: 'dashboard',
          title: '消息管理'
        }
      }
    ]
  },
  {
    path: '/redirect',
    component: Layout,
    hidden: true,
    children: [
      {
        path: '/redirect/:path(.*)',
        component: () => import('@/views/redirect/index')
      }
    ]
  },
  // {
  //   path: '/andon/monitor',
  //   name: 'AndonMonitor',
  //   component: () => import(/* webpackChunkName: "AndonMonitor" */ '@/views/andon/monitor'),
  //   hidden: true,
  //   meta: {
  //     icon: 'monitor',
  //     title: '看板监控'
  //   }
  // },
  {
    path: '/401',
    name: 'Page401',
    component: () => import(/* webpackChunkName: "Page401" */ '@/views/error-page/401.vue'),
    meta: {
      title: '401'
    },
    hidden: true
  },
  {
    path: '/404',
    name: 'Page404',
    component: () => import(/* webpackChunkName: "Page404" */ '@/views/error-page/404.vue'),
    meta: {
      title: '404'
    },
    hidden: true
  },
  {
    path: '/screen/analyticalOverview',
    name: 'WmsScreenAnalyticalOverview',
    component: () => import(/* webpackChunkName: "WmsScreenAnalyticalOverview" */ '@/views/screen/analyticalOverview'),
    meta: {
      title: '分析概览'
    },
    hidden: true
  },
  {
    path: '/screen/analyticalOverviewV1',
    name: 'WmsScreenAnalyticalOverviewV1',
    component: () => import(/* webpackChunkName: "WmsScreenAnalyticalOverviewV1" */ '@/views/screen/analyticalOverviewV1'),
    meta: {
      title: '中院大屏概览V1'
    },
    hidden: true
  },
  {
    path: '/screen/analyticalOverviewV2',
    name: 'WmsScreenAnalyticalOverviewV2',
    component: () => import(/* webpackChunkName: "WmsScreenAnalyticalOverviewV2" */ '@/views/screen/analyticalOverviewRealV2'),
    meta: {
      title: '中院大屏概览V2'
    },
    hidden: true
  },
  {
    // path: 'kanbanIndex',
    path: '/board/kanbanIndex',
    name: 'KanbanIndexSingle',
    // component: () => import(/* webpackChunkName: "KanbanIndexSingle" */ '@/views/board/kanbanIndex'),
    component: () => import(/* webpackChunkName: "KanbanIndexSingle" */ '@/views/screen/analyticalOverviewRealV2'),
    meta: {
      title: '看板'
    },
    hidden: true
  },
  {
    path: '/screen/boardOverviewV1',
    name: 'WmsBoardOverviewV1',
    component: () => import(/* webpackChunkName: "WmsBoardOverviewV1" */ '@/views/screen/boardOverviewV1'),
    meta: {
      title: '仓储看板'
    },
    hidden: true
  }
]
