import Vue from 'vue'
import { queryCodeDictDetailApi } from '@/apis/modules/common'

export default class Dict {
  constructor(dict) {
    this.dict = dict
  }

  async init(names, completeCallback) {
    if (names === undefined || names === null) {
      throw new Error('need Dict names')
    }
    // 单独获取字典
    const apis = []
    names.forEach(n => {
      Vue.set(this.dict, n, [])
      apis.push(queryCodeDictDetailApi({
        name: n
      }).then(res => {
        if (!res.code) {
          const { data } = res
          this.dict[n] = data !== null ? [...data.dictDetails] : []
        }
      }))
    })
    await Promise.all(apis)
    completeCallback()
  }
}
