<template>
  <router-link to="/message" class="fl bell-wrapper">
    <el-badge :max="99" :value="messageNum" :hidden="messageNum === 0 || messageNum === '0' || !messageNum">
      <svg-icon class="bell" icon-class="bell" font-size="20" />
    </el-badge>
  </router-link>
</template>

<script>
import { mapGetters } from 'vuex'
import SockJS from 'sockjs-client'
import Stomp from 'webstomp-client'

export default {
  name: 'DashBoardMessageComs',
  data() {
    return {
      socket: null,
      messageNum: 0
    }
  },
  computed: {
    ...mapGetters({
      token: 'token',
      userInfo: 'userInfo'
    })
  },
  mounted() {
    if ('WebSocket' in window) {
      this.connection()
    } else {
      alert('当前浏览器不支持 websocket')
    }
  },
  beforeDestroy() {
    this.destroy()
  },
  methods: {
    connection() {
      const socketUrl = `${process.env.VUE_APP_BASE_API_WMS}/StompSocket`
      const socket = new SockJS(socketUrl)
      const debug = process.env.NODE_ENV !== 'production'
      this.stompClient = Stomp.over(socket, { debug: debug })
      this.stompClient.connect({ Authorization: this.token }, (frame) => {
        console.log('连接成功了')
        this.socketSubscribe()
      }, (err) => {
        console.log('socket连接失败了', err)
        this.reconnect(socketUrl)
      })
    },
    // socket 订阅的消息列表
    socketSubscribe() {
      // 获取消息数量
      const url = `/user/${this.userInfo.id}/wms/unreadMsgNum`
      // '/topic/topMessage'
      this.stompClient.subscribe(url, (greeting) => {
        if (greeting.body) {
          console.log('《铃铛》发送回来的数据=========================》', greeting.body)
          this.messageNum = Number(greeting.body)
        }
      }, (err) => {
        console.log('连接房间后报错=====》', err)
      })
    },
    // socket断线重连
    reconnect(socketUrl) {
      console.log('socket in reconnect ...')
      let connected = false
      let connectTimes = 1
      const reconInv = setInterval(() => {
        console.info('in interval' + Math.random())
        const socket = new SockJS(socketUrl)
        const debug = process.env.NODE_ENV !== 'production'
        // const debug = true
        this.stompClient = Stomp.over(socket, { debug: debug })
        this.stompClient.connect({}, (frame) => {
          console.info('reconnected success')
          // 连接成功，清除定时器
          clearInterval(reconInv)
          connected = true
          this.socketSubscribe()
        }, () => {
          // 连接失败 计数
          connectTimes++
          console.log(`socket reconnect time is ${connectTimes} `)
          if (connectTimes > 10) {
            clearInterval(reconInv)
            connected = false
            console.log(`socket重连次数超过${connectTimes}次，停止连接`)
            this.$message({
              message: '用户消息接收失败，请刷新页面重试',
              type: 'error',
              duration: 2 * 1000
            })
          }
          console.info('reconnect failed')
          console.info('connected:' + connected)
          if (connected) {
            console.info('connect .... what?')
          }
        })
      }, 2000)
    },
    destroy() {
      if (this.stompClient) {
        // 断开socket连接
        this.stompClient.disconnect(() => {
          console.log('断开连接了')
        }, { Authorization: this.token })
      }
    }
  }
}
</script>

<style lang='scss' scoped>
</style>
