/*
 *   用户相关store
 *
 */
import { logOutApi } from '@/apis/modules/user'
import { getSidebarApi } from '@/apis/modules/user'
import {
  getToken,
  setToken,
  removeToken,
  getUserInfo,
  removeUserInfo,
  routesReduce,
  serverMapRoutes
} from '@/utils/auth'
import {
  resetRouter,
  constantRoutes,
  asyncRoutes
} from '@/routers'

const state = {
  token: getToken(),
  userInfo: getUserInfo('userInfo'), // 用户信息
  routes: [],
  addRoutes: [],
  roles: []
}

const getters = {
  token: state => state.token,
  userInfo: state => state.userInfo,
  routes: state => state.routes,
  addRoutes: state => state.addRoutes,
  roles: state => state.roles
}

const mutations = {
  SET_TOKEN: (state, token) => {
    setToken(token)
    state.token = token
  },
  SET_USER_INFO: (state, userInfo) => {
    state.userInfo = userInfo
  },
  REMOVE_USER_INFO: (state) => {
    state.userInfo = {
      username: '',
      photo: ''
    }
  },
  SET_ROUTES: (state, routes) => {
    state.addRoutes = routes
    // state.routes = routes.concat(constantRoutes)
    state.routes = constantRoutes.concat(routes)
  },
  REMOVE_ROUTES: (state, routes) => {
    state.routes = []
    state.addRoutes = []
  },
  SET_ROLES: (state, role) => {
    const roles = state.roles.concat(role)
    state.roles = roles
  }
}

const actions = {
  // 获取后台菜单并整理成前端路由
  GenerateRoutes({
    commit
  }) {
    return new Promise((resolve, reject) => {
      getSidebarApi().then(res => {
        const { user, menus } = res.data
        const userInfo = user
        commit('SET_USER_INFO', userInfo)
        const routesMapJson = routesReduce(asyncRoutes)
        const accessedRoutes = serverMapRoutes(menus, routesMapJson)
        accessedRoutes.push({
          path: '*',
          redirect: '/404'
        })
        commit('SET_ROUTES', accessedRoutes)
        resolve(accessedRoutes)
      }).catch(e => {
        reject(e)
      })
    })
  },
  // 退出登录
  LogOut({
    commit
  }) {
    return new Promise((resolve, reject) => {
      logOutApi().then(res => {
        removeToken()
        removeUserInfo()
        commit('SET_TOKEN', '')
        commit('REMOVE_USER_INFO')
        commit('REMOVE_ROUTES')
        resetRouter()
        resolve(res)
      }).catch(err => {
        reject(err)
        console.log(err)
      })
    })
  },
  // 异常状态踢出登录
  resetToken({ commit }) {
    return new Promise(resolve => {
      commit('SET_TOKEN', '')
      commit('REMOVE_USER_INFO')
      commit('REMOVE_ROUTES')
      removeToken()
      resolve()
    })
  }
}

export default {
  state,
  mutations,
  actions,
  getters
}
