import Layout from '@/layout/Default'

export default [
  {
    path: '/tools',
    redirect: '/tools/icons',
    component: Layout,
    meta: {
      icon: 'dashboard',
      title: '开发工具'
    },
    children: [
      {
        path: 'icons',
        name: 'ToolsIcons',
        component: () => import('@/views/tools/icons'),
        meta: {
          icon: 'dashboard',
          title: '图标'
        }
      },
      {
        path: 'table',
        name: 'ToolsTable',
        component: () => import('@/views/tools/table'),
        meta: {
          icon: 'dashboard',
          title: '列表'
        }
      }
    ]
  }
]
