import Layout from '@/layout/Default'

export default {
  path: '/out-bound',
  component: Layout,
  name: 'OutBoundModule',
  redirect: '/out-bound/reservation-form',
  meta: {
    icon: '',
    title: '出库管理'
  },
  children: [
    {
      path: 'reservation-form',
      name: 'OutBoundReservationForm',
      component: () => import(/* webpackChunkName: "OutBoundReservationForm" */'@/views/out-bound/reservation-form'),
      meta: {
        icon: '',
        title: '预留单'
      }
    },
    {
      path: '/reserved-suborder-audit/:id',
      name: 'BillReservedSubAudit',
      component: () => import(/* webpackChunkName: "BillReservedSubAudit" */'@/views/out-bound/reservation-form/childEdit'),
      meta: {
        icon: '',
        title: '预留子单审核'
      }
    },
    {
      path: '/reserved-suborder-add',
      name: 'BillReservedSubAdd',
      component: () => import(/* webpackChunkName: "BillReservedSubAdd" */'@/views/out-bound/reservation-form/childEdit'),
      meta: {
        icon: '',
        title: '预留子单新增'
      }
    },
    {
      path: '/reserved-suborder-edit',
      name: 'BillReservedSubEdit',
      component: () => import(/* webpackChunkName: "BillReservedSubEdit" */'@/views/out-bound/reservation-form/childEdit'),
      meta: {
        icon: '',
        title: '预留子单编辑'
      }
    },
    {
      path: '/reserved-suborder-detail',
      name: 'BillReservedSubDetail',
      component: () => import(/* webpackChunkName: "BillReservedSubDetail" */'@/views/out-bound/reservation-form/childEdit'),
      meta: {
        icon: '',
        title: '预留子单详情'
      }
    },
    {
      path: 'reservation-form/detail/:id/:childrenType',
      name: 'OutBoundReservationFormDetail',
      component: () => import(/* webpackChunkName: "OutBoundReservationFormDetail" */'@/views/out-bound/reservation-form/detail'),
      props: route => ({ id: route.params.id }),
      meta: {
        icon: '',
        title: '预留单详情'
      }
    },
    {
      path: 'reservation-form/split/:id',
      name: 'OutBoundReservationFormSplit',
      component: () => import(/* webpackChunkName: "OutBoundReservationFormSplit" */'@/views/out-bound/reservation-form/split'),
      props: route => ({ id: route.params.id }),
      meta: {
        icon: '',
        title: '预留单拆分'
      }
    },
    {
      path: 'material-requisition',
      name: 'OutBoundMaterialRequisition',
      component: () => import(/* webpackChunkName: "OutBoundMaterialRequisition" */'@/views/out-bound/material-requisition'),
      meta: {
        icon: '',
        title: '领料单'
      }
    },
    {
      path: 'material-requisition/detail/:billNumber',
      name: 'OutBoundMaterialRequisitionDetail',
      component: () => import(/* webpackChunkName: "OutBoundMaterialRequisitionDetail" */'@/views/out-bound/material-requisition/detail'),
      meta: {
        icon: '',
        title: '领料单详情'
      }
    },
    {
      path: 'material-requisition/split/:billNumber',
      name: 'OutBoundMaterialRequisitionSplit',
      component: () => import(/* webpackChunkName: "OutBoundMaterialRequisitionSplit" */'@/views/out-bound/material-requisition/split'),
      meta: {
        icon: '',
        title: '领料单拆分'
      }
    },
    {
      path: 'material-requisition/subOrderEdit',
      name: 'OutBoundMaterialRequisitionSubOrderEdit',
      component: () => import(/* webpackChunkName: "OutBoundMaterialRequisitionSubOrderEdit" */'@/views/out-bound/material-requisition/suborderEdit'),
      meta: {
        icon: '',
        title: '领料子单编辑'
      }
    },
    {
      path: 'material-requisition/subOrderDetail',
      name: 'OutBoundMaterialRequisitionSubOrderDetail',
      component: () => import(/* webpackChunkName: "OutBoundMaterialRequisitionSubOrderDetail" */'@/views/out-bound/material-requisition/suborderEdit'),
      meta: {
        icon: '',
        title: '领料子单详情'
      }
    },
    {
      path: 'outbound-information',
      name: 'OutBoundInformation',
      component: () => import(/* webpackChunkName: "OutBoundInformation" */'@/views/out-bound/outbound-information'),
      meta: {
        icon: '',
        title: '出库信息'
      }
    },
    // Tip 携带出库单信息的单号跟类型
    {
      path: 'outBound-information/warehouseOut/:billNumber/:billType',
      name: 'OutBoundInfoWarehouseOut',
      component: () => import(/* webpackChunkName: "OutBoundInfoWarehouseOut" */'@/views/out-bound/outbound-information/warehouseOut'),
      meta: {
        icon: '',
        title: '新增出库信息'
      }
    },
    {
      path: 'outBound-information/detail/:billNumber/:billType',
      name: 'OutBoundInfoDetail',
      component: () => import(/* webpackChunkName: "OutBoundInfoDetail" */'@/views/out-bound/outbound-information/detail'),
      meta: {
        icon: 'codeConsole',
        title: '出库信息详情'
      }
    },
    {
      path: 'outbound-information/receipt/:billNumber/:billType/:warehouseOutOrderNo/:materialIds',
      name: 'OutBoundInfoReceipt',
      component: () => import(/* webpackChunkName: "OutBoundInfoReceipt" */'@/views/out-bound/outbound-information/receipt'),
      meta: {
        icon: '',
        title: '出库单据信息'
      }
    },
    // Tip 携带出库单单号
    {
      path: 'outbound-information/delivery/:billNumber',
      name: 'OutBoundDelivery',
      component: () => import(/* webpackChunkName: "OutBoundDelivery" */'@/views/out-bound/outbound-information/delivery'),
      meta: {
        icon: '',
        title: '出库单信息'
      }
    },
    {
      path: 'outbound-information/edit/:billNumber',
      name: 'OutBoundInfoEdit',
      component: () => import(/* webpackChunkName: "OutBoundInfoEdit" */'@/views/out-bound/outbound-information/edit'),
      meta: {
        icon: '',
        title: '出库单信息编辑'
      }
    },
    {
      path: 'stock-return',
      name: 'OutBoundStockReturn',
      component: () => import(/* webpackChunkName: "OutBoundStockReturn" */'@/views/out-bound/stock-return'),
      meta: {
        icon: '',
        title: '退库'
      }
    },
    {
      path: 'stock-return/addStockReturnInfo',
      name: 'WmsOutBoundAddStockReturnInfo',
      component: () => import(/* webpackChunkName: "WmsOutBoundAddStockReturnInfo" */'@/views/out-bound/stock-return/addStockReturnInfo'),
      meta: {
        icon: '',
        title: '新增退库信息'
      }
    },
    {
      path: 'stock-return/stockReturnOrderInfo/:billNumber', // 退库单号
      name: 'WmsOutBoundStockReturnOrderInfo',
      component: () => import(/* webpackChunkName: "WmsOutBoundStockReturnOrderInfo" */'@/views/out-bound/stock-return/stockReturnOrderInfo'),
      meta: {
        icon: '',
        title: '退库单据信息'
      }
    },
    {
      path: 'stock-return/edit/:billNumber',
      name: 'WmsOutBoundStockEdit',
      props: route => ({ billNumber: route.params.billNumber }),
      component: () => import(/* webpackChunkName: "WmsOutBoundStockEdit" */'@/views/out-bound/stock-return/edit'),
      meta: {
        icon: '',
        title: '退库编辑'
      }
    },
    {
      path: 'stock-return/detail/:billNumber',
      name: 'WmsOutBoundStockDetail',
      props: route => ({ billNumber: route.params.billNumber }),
      component: () => import(/* webpackChunkName: "WmsOutBoundStockDetail" */'@/views/out-bound/stock-return/edit'),
      meta: {
        icon: '',
        title: '退库详情'
      }
    },
    {
      path: 'return-of-goods',
      name: 'OutBoundReturnOfGoods',
      component: () => import(/* webpackChunkName: "OutBoundReturnOfGoods" */'@/views/out-bound/return-of-goods'),
      meta: {
        icon: '',
        title: '退货'
      }
    },
    {
      path: 'return-of-goods-audit/:id',
      name: 'BillReturnMaterialsAudit',
      component: () => import(/* webpackChunkName: "BillReturnMaterialsAudit" */'@/views/out-bound/return-of-goods/edit'),
      meta: {
        icon: '',
        title: '退货单审核'
      }
    },
    {
      path: 'return-of-goods-add',
      name: 'BillReturnMaterialsAdd',
      component: () => import(/* webpackChunkName: "BillReturnMaterialsAdd" */'@/views/out-bound/return-of-goods/edit'),
      meta: {
        icon: '',
        title: '退货单新增'
      }
    },
    {
      path: 'return-of-goods-edit',
      name: 'BillReturnMaterialsEdit',
      component: () => import(/* webpackChunkName: "BillReturnMaterialsEdit" */'@/views/out-bound/return-of-goods/edit'),
      meta: {
        icon: '',
        title: '退货单编辑'
      }
    },
    {
      path: 'return-of-goods-detail',
      name: 'BillReturnMaterialsDetail',
      component: () => import(/* webpackChunkName: "BillReturnMaterialsDetail" */'@/views/out-bound/return-of-goods/edit'),
      meta: {
        icon: '',
        title: '退货单详情'
      }
    },
    {
      path: 'outsourcing',
      name: 'OutBoundOutsourcing',
      component: () => import(/* webpackChunkName: "OutBoundOutsourcing" */'@/views/out-bound/outsourcing'),
      meta: {
        icon: '',
        title: '外协出库'
      }
    },
    {
      path: 'outsource-outboundDeliveryOrder-audit/:id',
      name: 'BillOutsourceOutAudit',
      component: () => import(/* webpackChunkName: "BillOutsourceOutAudit" */'@/views/out-bound/outsourcing/edit'),
      meta: {
        icon: '',
        title: '外协出库单审核'
      }
    },
    {
      path: 'outsource-outboundDeliveryOrder-add',
      name: 'BillOutsourceOutAdd',
      component: () => import(/* webpackChunkName: "BillOutsourceOutAdd" */'@/views/out-bound/outsourcing/edit'),
      meta: {
        icon: '',
        title: '外协出库新增'
      }
    },
    {
      path: 'outsource-outboundDeliveryOrder-edit',
      name: 'BillOutsourceOutEdit',
      component: () => import(/* webpackChunkName: "BillOutsourceOutEdit" */'@/views/out-bound/outsourcing/edit'),
      meta: {
        icon: '',
        title: '外协出库编辑'
      }
    },
    {
      path: 'outsource-outboundDeliveryOrder-detail',
      name: 'BillOutsourceOutDetail',
      component: () => import(/* webpackChunkName: "BillOutsourceOutDetail" */'@/views/out-bound/outsourcing/edit'),
      meta: {
        icon: '',
        title: '外协出库详情'
      }
    }
  ]
}
