import Cookies from 'js-cookie'
import defaultSettings from '@/settings'

const { tagsView, fixedHeader, sidebarLogo } = defaultSettings

const state = {
  sidebar: {
    opened: Cookies.get('sidebarStatus') ? !!+Cookies.get('sidebarStatus') : true,
    withoutAnimation: false
  },
  device: 'desktop',
  size: Cookies.get('size') || 'medium',
  tagsView: tagsView,
  fixedHeader: fixedHeader,
  sidebarLogo: sidebarLogo,
  // 导航栏仓库选中列表
  navbarWarehouseCheckedList: [],
  // 移位绑定 库位
  yiweiBindStorageLocation: [],
  nightMode: false // 夜间模式
}

const getters = {
  sidebar: state => state.sidebar,
  device: state => state.device,
  size: state => state.size,
  fixedHeader: state => state.fixedHeader,
  tagsView: state => state.tagsView,
  sidebarLogo: state => state.sidebarLogo,
  // Tip 当前仓库
  navbarWarehouseCheckedList: state => state.navbarWarehouseCheckedList,
  yiweiBindStorageLocation: state => state.yiweiBindStorageLocation,
  nightMode: state => state.nightMode
}

const mutations = {
  TOGGLE_SIDEBAR: state => {
    state.sidebar.opened = !state.sidebar.opened
    state.sidebar.withoutAnimation = false
    if (state.sidebar.opened) {
      Cookies.set('sidebarStatus', 1)
    } else {
      Cookies.set('sidebarStatus', 0)
    }
  },
  CLOSE_SIDEBAR: (state, withoutAnimation) => {
    Cookies.set('sidebarStatus', 0)
    state.sidebar.opened = false
    state.sidebar.withoutAnimation = withoutAnimation
  },
  TOGGLE_DEVICE: (state, device) => {
    state.device = device
  },
  SET_SIZE: (state, size) => {
    state.size = size
    Cookies.set('size', size)
  },
  CHANGE_SETTING: (state, { key, value }) => {
    if (state.hasOwnProperty(key)) {
      state[key] = value
    }
  },
  // Tip 切换仓库
  TOGGLE_NAVBAR_WAREHOUSE: (state, list) => {
    state.navbarWarehouseCheckedList = list
  },
  // Tip 切换夜间模式
  TOGGLE_NIGHT_Mode: (state, mode) => {
    state.nightMode = mode
  },
  ADD_YIWEI_BIND_SL: (state, info) => {
    state.yiweiBindStorageLocation.push(info)
  },
  DEL_YIWEI_BIND_SL: (state, info) => {
    state.yiweiBindStorageLocation = state.yiweiBindStorageLocation.filter(c => c.barCode !== info.barCode)
  },
  CLEAN_YIWEI_BIND_SL: (state, info) => {
    state.yiweiBindStorageLocation = []
  }
}

const actions = {
  toggleSideBar({ commit }) {
    commit('TOGGLE_SIDEBAR')
  },
  closeSideBar({ commit }, { withoutAnimation }) {
    commit('CLOSE_SIDEBAR', withoutAnimation)
  },
  toggleDevice({ commit }, device) {
    commit('TOGGLE_DEVICE', device)
  },
  setSize({ commit }, size) {
    commit('SET_SIZE', size)
  },
  changeSetting({ commit }, data) {
    commit('CHANGE_SETTING', data)
  },
  // Tip 切换仓库
  toggleWarehouse({ commit }, list) {
    commit('TOGGLE_NAVBAR_WAREHOUSE', list)
  },
  addYiWeiBindSl({ commit }, info) {
    commit('ADD_YIWEI_BIND_SL', info)
  },
  delYiWeiBindSl({ commit }, info) {
    commit('DEL_YIWEI_BIND_SL', info)
  },
  cleanYiWeiBindSl({ commit }) {
    commit('CLEAN_YIWEI_BIND_SL')
  }
}

export default {
  namespaced: true,
  state,
  getters,
  mutations,
  actions
}
