import store from '@/store'

export default {
  beforeMount() {
    window.addEventListener('resize', this.$_resizeHandler)
  },
  beforeDestroy() {
    window.removeEventListener('resize', this.$_resizeHandler)
  },
  methods: {
    $_resizeHandler() {
      if (!document.hidden) {
        store.dispatch('app/toggleDevice', 'desktop')
      }
    }
  }
}
