import Layout from '@/layout/Default'

export default {
  path: '/client',
  component: Layout,
  name: 'WMSClientModule',
  redirect: '/client/clientManager',
  meta: {
    icon: '',
    title: '客户端'
  },
  children: [
    {
      path: 'clientManager',
      name: 'ClientManager',
      component: () => import(/* webpackChunkName: "ClientManager" */'@/views/client/clientManager'),
      meta: {
        icon: '',
        title: '客户端管理'
      }
    }
  ]
}
