<template>
  <div class="navbar">
    <hamburger id="hamburger-container" :is-active="sidebar.opened" class="hamburger-container" @toggleClick="toggleSideBar" />
    <!-- 面包屑导航 -->
    <breadcrumb id="breadcrumb-container" class="breadcrumb-container" />

    <div class="right-menu clearfix">
      <div class="fl">
        <el-select
          v-model="query.warehouseIds"
          multiple
          collapse-tags
          filterable
          clearable
          placeholder="仓库名称"
          style="width: 178px;margin-right: 15px;"
          @change="handleSelectChange"
        >
          <el-option
            v-for="(item,index) in warehousePullList"
            :key="index"
            :label="item.name"
            :value="item.id"
          />
        </el-select>
      </div>
      <!-- 全屏 -->
      <screenfull id="screenfull" class="right-menu-item hover-effect fl" />
      <!-- 首页 -->
      <i class="fl home" @click="goDashboard" />
      <!-- 当前用户 -->
      <div v-show="userInfo.nickName" class="fl userinfo">
        <span>欢迎，</span>
        <span>{{ userInfo.nickName }} ！</span>
      </div>
      <!-- 主题切换 -->
      <el-button type="text" style="font-size: 16px;" @click="toggleNightMode">
        <svg-icon v-if="!nightMode" icon-class="night" />
        <svg-icon v-else icon-class="day" />
      </el-button>
      <!-- 导航待办 -->
      <div class="fl to-do" style="cursor: pointer" @click="dialogTableVisible = true">待办({{ dealtLen }})</div>
      <!-- 消息提示 -->
      <message />
      <!-- 订阅待办 -->
      <todo-message @getDealtLen="getDealLength" />
      <!-- 退出登录 -->
      <el-dropdown size="medium">
        <span class="el-dropdown-link">
          <img class="fl userlogo" :src="userInfo.photo ? userInfo.photo : userlogo" alt="用户头像">
          <i class="el-icon-caret-bottom el-icon--right mr20" />
        </span>
        <el-dropdown-menu slot="dropdown">
          <!-- <router-link to="/profile/user-info">
            <el-dropdown-item>修改密码</el-dropdown-item>
          </router-link> -->
          <el-dropdown-item>
            <span style="display:block;" @click="logout">退出登录</span>
          </el-dropdown-item>
        </el-dropdown-menu>
      </el-dropdown>
    </div>
    <todo-list-dialog
      v-model="dialogTableVisible"
      @sendLength="getDealLength"
    />
  </div>
</template>

<script>
import { mapActions, mapGetters, mapMutations } from 'vuex'
import { getLoginPath } from '@/utils/auth'
import Hamburger from '@/components/Hamburger'
import Breadcrumb from '@/components/Breadcrumb'
import Screenfull from '@/components/Screenfull'
import Message from '@/components/Message'
import TodoMessage from '@/components/TodoMessage'
import { getWarehouseSapPullListApi } from '@/apis/modules/base/basewmsWarehouse'
import TodoListDialog from '@/views/dashboard/components/TodoList'

export default {
  components: {
    Hamburger,
    Breadcrumb,
    Screenfull,
    Message,
    TodoMessage,
    TodoListDialog
  },
  data() {
    return {
      username: '退出登录',
      userlogo: require('@/assets/images/layout/mainLayout/userlogo.png'),
      warehousePullList: [],
      query: {
        warehouseIds: []
      },
      dialogTableVisible: false,
      dealtLen: 0
    }
  },
  computed: {
    ...mapGetters({
      sidebar: 'app/sidebar',
      nightMode: 'app/nightMode',
      userInfo: 'userInfo'
    })
  },
  mounted() {
    this.getWarehousePullList()
  },
  methods: {
    ...mapActions('app', {
      _toggleWarehouse: 'toggleWarehouse'
    }),
    ...mapMutations('app', {
      'TOGGLE_NIGHT_Mode': 'TOGGLE_NIGHT_Mode'
    }),
    toggleNightMode() {
      this.TOGGLE_NIGHT_Mode(!this.nightMode)
      this.$nextTick(() => {
        const htmlEle = document.querySelector('html')
        const imgEle = document.querySelector('img')
        if (this.nightMode) {
          !htmlEle.hasAttribute('data-theme') && htmlEle.setAttribute('data-theme', 'dark')
          !imgEle.hasAttribute('data-theme') && imgEle.setAttribute('data-theme', 'dark')
          return
        }
        htmlEle.hasAttribute('data-theme') && htmlEle.removeAttribute('data-theme')
        imgEle.hasAttribute('data-theme') && imgEle.removeAttribute('data-theme')
      })
    },
    // 下拉数据
    getWarehousePullList() {
      return new Promise((resolve, reject) => {
        getWarehouseSapPullListApi().then(res => {
          const { code, data } = res
          if (!code) {
            this.warehousePullList = data || []
            // this.warehousePullList.unshift({ id: '', name: '全部' })
          }
          resolve(res)
        }).catch(err => {
          reject(err)
        })
      })
    },
    // 下拉框选中值改变的回调
    handleSelectChange(bindVal) {
      const warehouseCheckedList = this.warehousePullList.filter(item => bindVal.includes(item.id))
      this._toggleWarehouse(warehouseCheckedList || [])
    },
    toggleSideBar() {
      this.$store.dispatch('app/toggleSideBar')
    },
    logout() {
      this.$store.dispatch('LogOut').then(() => {
        window.location.href = getLoginPath()
      })
    },
    // 跳转到首页
    goDashboard() {
      this.$router.push('/')
    },
    getDealLength(n) {
      this.dealtLen = n
    }
  }
}
</script>

<style lang="scss" scoped>
.navbar {
  height: 50px;
  overflow: hidden;
  position: relative;
  background: #fff;
  box-shadow: 0 1px 4px rgba(0, 21, 41, 0.08);

  .hamburger-container {
    line-height: 46px;
    height: 100%;
    float: left;
    cursor: pointer;
    transition: background-color 0.3s;
    -webkit-tap-highlight-color: transparent;

    &:hover {
      background: rgba(0, 0, 0, 0.025);
    }
  }

  .breadcrumb-container {
    float: left;
    line-height: 50px;
  }

  .errLog-container {
    display: inline-block;
    vertical-align: top;
  }

  .right-menu {
    float: right;
    height: 100%;
    line-height: 50px;

    &:focus {
      outline: none;
    }

    .right-menu-item {
      display: inline-block;
      margin: 0 18px;
      // 语言切换按钮,悬停执行动画(背景颜色过渡)
      &.hover-effect {
        cursor: pointer;
        transition: background-color 0.3s;

        &:hover {
          background: rgba(0, 0, 0, 0.025);
        }
      }
    }

    .home {
      display: block;
      width: 24px;
      height: 24px;
      background: url('~@/assets/images/layout/mainLayout/home.png') no-repeat
        center;
      background-size: 100% 100%;
      margin: 12px 9px 0 0;
      cursor: pointer;
    }
    .userinfo {
      font-size: 14px;
      padding-left: 9px;
      border-left: 2px solid #bcbcbc;
      line-height: 20px;
      margin-top: 15px;
    }
    .bell-wrapper {
      line-height: 1;
      margin-top: 15px;
      margin: 15px 8px 0 8px;
      cursor: pointer;
      .bell {
        font-size: 20px;
      }
    }
    .userlogo {
      width: 30px;
      height: 30px;
      margin: 9px 0 0 14px;
      border-radius: 100%;
    }
    .mr20 {
      margin-right: 20px;
    }
    .logout-item {
      color: #666;
      margin-right: 20px;
      font-size: 14px;
      cursor: pointer;
    }
    /deep/ .el-button--text {
      margin-left: 5px;
      color: #666;
      margin-top: 5px;
    }
    /deep/ .el-dropdown-link {
      cursor: pointer;
    }
  }
}
</style>
