import Layout from '@/layout/Default'

export default {
  path: '/warehouse',
  component: Layout,
  name: 'WarehouseModule',
  redirect: '/warehouse/inventory',
  meta: {
    icon: 'delivery',
    title: '仓库管理'
  },
  children: [
    {
      path: 'inventory',
      name: 'WarehouseInventory',
      component: () => import(/* webpackChunkName: "WarehouseInventory" */'@/views/warehouse/inventory'),
      props: route => ({ materialCode: route.params.materialCode || '' }),
      meta: {
        icon: 'codeConsole',
        title: '库存台账'
      }
    },
    {
      path: 'inventory-detail/:materialId',
      name: 'WarehouseInventoryDetail',
      component: () => import(/* webpackChunkName: "WarehouseInventoryDetail" */'@/views/warehouse/inventory/detail/index'),
      props: route => ({ materialId: route.params.materialId }),
      meta: {
        icon: '',
        title: '库存台账详情'
      }
    },
    {
      path: 'chooseWarehouseLocation',
      name: 'WarehouseChooseWarehouseLocation',
      component: () => import(/* webpackChunkName: "WarehouseChooseWarehouseLocation" */'@/views/warehouse/chooseWarehouseLocation/index'),
      meta: {
        icon: '',
        title: '选择库位'
      }
    },
    {
      path: 'check',
      name: 'WarehouseCheck',
      component: () => import(/* webpackChunkName: "WarehouseCheck" */'@/views/warehouse/check'),
      meta: {
        icon: 'exam-result',
        title: '盘点'
      }
    },
    {
      path: 'discharge',
      name: 'WarehouseDischarge',
      component: () => import(/* webpackChunkName: "WarehouseDischarge" */'@/views/warehouse/discharge'),
      meta: {
        icon: 'backup',
        title: '移库调拨单'
      }
    },
    {
      path: 'transfer-order-audit/:id',
      name: 'BillTransferAudit',
      component: () => import(/* webpackChunkName: "BillTransferAudit" */'@/views/warehouse/discharge/edit'),
      meta: {
        icon: '',
        title: '调拨单审核'
      }
    },
    {
      path: 'transfer-order-detail',
      name: 'BillTransferDetail',
      component: () => import(/* webpackChunkName: "BillTransferDetail" */'@/views/warehouse/discharge/edit'),
      meta: {
        icon: '',
        title: '调拨单详情'
      }
    },
    {
      path: 'transfer-order-add',
      name: 'BillTransferAdd',
      component: () => import(/* webpackChunkName: "BillTransferAdd" */'@/views/warehouse/discharge/edit'),
      meta: {
        icon: '',
        title: '调拨单新增'
      }
    },
    {
      path: 'transfer-order-edit',
      name: 'BillTransferEdit',
      component: () => import(/* webpackChunkName: "BillTransferEdit" */'@/views/warehouse/discharge/edit'),
      meta: {
        icon: '',
        title: '调拨单编辑'
      }
    },
    {
      path: 'disposition',
      name: 'WarehouseDisposition',
      component: () => import(/* webpackChunkName: "WarehouseDisposition" */'@/views/warehouse/disposition'),
      meta: {
        icon: 'drag',
        title: '移位'
      }
    },
    {
      path: 'label',
      name: 'WarehouseLabel',
      component: () => import(/* webpackChunkName: "WarehouseLabel" */'@/views/warehouse/label'),
      meta: {
        icon: 'chain',
        title: '油墨标签'
      }
    },
    {
      path: 'barcodeManage',
      name: 'WarehouseBarcodeManage',
      component: () => import(/* webpackChunkName: "WarehouseBarcodeManage" */'@/views/warehouse/barcodeManage'),
      meta: {
        icon: '',
        title: '条码管理'
      }
    }
  ]
}
