import Layout from '@/layout/Default'

export default {
  path: '/base',
  component: Layout,
  name: 'WMSBaseModule',
  redirect: '/base/BaseCustomerData',
  meta: {
    icon: 'database',
    title: '基础数据'
  },
  children: [
    {
      path: 'customer-data',
      name: 'WMSBaseCustomerData',
      component: () => import(/* webpackChunkName: "WMSBaseCustomerData" */'@/views/base/customer-data'),
      meta: {
        icon: 'role',
        title: '客户'
      }
    },
    {
      path: 'supplier',
      name: 'WMSBaseSupplier',
      component: () => import(/* webpackChunkName: "WMSBaseSupplier" */'@/views/base/supplier'),
      meta: {
        icon: 'chart',
        title: '供应商'
      }
    },
    {
      path: 'factory',
      name: 'WMSBaseFactory',
      component: () => import(/* webpackChunkName: "WMSBaseFactory" */'@/views/base/factory'),
      meta: {
        icon: 'icon',
        title: '工厂'
      }
    },
    {
      path: 'project',
      name: 'WMSBaseProject',
      component: () => import(/* webpackChunkName: "WMSBaseProject" */'@/views/base/project'),
      meta: {
        icon: 'table',
        title: '项目'
      }
    },
    {
      path: 'cost-center',
      name: 'WMSBaseCostCenter',
      component: () => import(/* webpackChunkName: "WMSBaseCostCenter" */'@/views/base/cost-center'),
      meta: {
        icon: 'doc',
        title: '成本中心'
      }
    },
    {
      path: 'transfer-type',
      name: 'WMSBaseTransferType',
      component: () => import(/* webpackChunkName: "WMSBaseTransferType" */'@/views/base/transfer-type'),
      meta: {
        icon: 'warehouse',
        title: '移库类型'
      }
    },
    {
      path: 'unit',
      name: 'WMSBaseUnit',
      component: () => import(/* webpackChunkName: "WMSBaseUnit" */'@/views/base/unit'),
      meta: {
        icon: 'tree',
        title: '单位'
      }
    },
    {
      path: 'item-group',
      name: 'WMSBaseItemGroup',
      component: () => import(/* webpackChunkName: "WMSBaseItemGroup" */'@/views/base/item-group'),
      meta: {
        icon: 'init',
        title: '物料组'
      }
    },
    {
      path: 'material-information',
      name: 'WMSBaseMaterialInformation',
      component: () => import(/* webpackChunkName: "WMSBaseMaterialInformation" */'@/views/base/material-information'),
      meta: {
        icon: 'error',
        title: '物料信息'
      }
    },
    {
      path: 'warehouse',
      name: 'WMSBasewmsWarehouse',
      component: () => import(/* webpackChunkName: "WMSBasewmsWarehouse" */'@/views/base/warehouse'),
      meta: {
        icon: 'visits',
        title: 'SAP仓库'
      }
    },
    {
      path: 'sapWarehouse',
      name: 'SAPBasewmsWarehouse',
      component: () => import(/* webpackChunkName: "SAPBasewmsWarehouse" */'@/views/base/sapWarehouse'),
      meta: {
        icon: 'ipvisits',
        title: '仓库'
      }
    },
    {
      path: 'warehouse-location',
      name: 'WMSBaseWarehouseLocation',
      component: () => import(/* webpackChunkName: "WMSBaseWarehouseLocation" */'@/views/base/warehouse-location'),
      meta: {
        icon: 'tab',
        title: '库位'
      }
    },
    {
      path: 'operationLog',
      name: 'WMSBaseOperationLog',
      component: () => import(/* webpackChunkName: "WMSBaseOperationLog" */'@/views/base/operationLog'),
      meta: {
        icon: 'log',
        title: '操作日志'
      }
    }
  ]
}
