import Vue from 'vue'
import Router from 'vue-router'
import $store from '../store'
import NProgress from 'nprogress' // progress bar
import 'nprogress/nprogress.css' // progress bar style
// 获取登录地址的工具函数
import { getLoginPath } from '@/utils/auth'

// import FullScreen from './modules/fullScreen.js'
import BaseRoutes from './modules/constant.js'
import ToolRoutes from './modules/tool.js'
// import base from './modules/baserouter.js'
// import Bomrouter from './modules/bom.router.js'
// console.log(base)
/**
 *  routers-module
 */
import routerModules from './modules'

Vue.use(Router)

let ConstantRoutes = []

process.env.NODE_ENV === 'development' ? ConstantRoutes = [...BaseRoutes, ...ToolRoutes] : ConstantRoutes = [...BaseRoutes]

NProgress.configure({
  // 不显示环形进度动画
  showSpinner: false
}) // NProgress Configuration

/**
 * 配置路由须知: sub-menu only appear when route children.length >= 1
 *
 * hidden: true                   if set true, item will not show in the sidebar(default is false)
 * alwaysShow: true               if set true, will always show the root menu
 *                                if not set alwaysShow, when item has more than one children route,
 *                                it will becomes nested mode, otherwise not show the root menu
 * redirect: noRedirect           if set noRedirect will no redirect in the breadcrumb
 * name:'router-name'             the name is used by <keep-alive> (must set!!!)
 * meta : {
    roles: ['admin','editor']    control the page roles (you can set multiple roles)
    title: 'title'               the name show in sidebar and breadcrumb (recommend set)
    icon: 'svg-name'             the icon show in the sidebar
    noCache: true                if set true, the page will no be cached(default is false)
    affix: true                  if set true, the tag will affix in the tags-view
    breadcrumb: false            if set false, the item will hidden in breadcrumb(default is true)
    activeMenu: '/example/list'  if set path, the sidebar will highlight the path you set

    按钮权限判断                  this.moudule = this.routesReduceJson[this.$route.name].meta.resoure_type
  }
 */

/**
 *  constantRoutes
 *  基本静态路由，不需要权限校验的路由列表
 *  所有用户都可以访问
 */
export const constantRoutes = [
  ...ConstantRoutes
]

/**
 *  asyncRoutes
 *  动态路由，根据后台登录返回的菜单列表动态选取
 *  不是所有用户都可以访问
 */
export const asyncRoutes = [
  ...routerModules
]

const createRouter = () => new Router({
  mode: 'history', // require service support
  base: process.env.VUE_APP_BASE_PUBLIC_PATH,
  routes: ConstantRoutes
})

const router = createRouter()

// Detail see: https://github.com/vuejs/vue-router/issues/1234#issuecomment-357941465
export function resetRouter() {
  const newRouter = createRouter()
  router.matcher = newRouter.matcher // reset router
}

// 不拦截登录白名单
const whileList = ['/401', '/404', '/andon/monitor', '/screen/monitor', '/screen/analyticalOverview', '/screen/analyticalOverviewV1', '/screen/analyticalOverviewV2', '/screen/boardOverviewV1', '/board/kanbanIndex']
// 全局前置守卫
router.beforeEach(async(to, from, next) => {
  NProgress.start() // 开启进度条
  const hasToken = $store.getters.token
  // 判断是否登录
  if (hasToken) {
    if (whileList.indexOf(to.path) !== -1) {
      next()
    } else {
      const hasMenu = $store.getters.routes && $store.getters.routes.length > 0
      if (hasMenu) {
        // 有菜单
        next()
      } else {
        // 无菜单或者用户刷新页面
        try {
          // 获取菜单列表
          // 根据后台菜单列表生成完整前端路由
          const accessRoutes = await $store.dispatch('GenerateRoutes')
          // 动态生成路由
          router.addRoutes(accessRoutes)
          // hack method to ensure that addRoutes is complete
          // set the replace: true, so the navigation will not leave a history record
          next({ ...to, replace: true })
          NProgress.done()
        } catch (error) {
          console.log(error)
          await $store.dispatch('resetToken')
          const loginUrl = getLoginPath()
          console.log(loginUrl)
          // if (loginUrl) {
          //   window.location.href = getLoginPath()
          // } else {
          //   next(`/404?redirect=${to.path}`)
          // }
          NProgress.done()
        }
      }
    }
  } else {
    // 未登录 登录拦截
    if (whileList.indexOf(to.path) !== -1) {
      next()
    } else {
      window.location.href = getLoginPath()
    }
    NProgress.done()
  }
})

router.afterEach(() => {
  window.scrollTo(0, 0)
  // finish progress bar
  NProgress.done()
})

export default router
