import request from '@/apis/request'
import requestWms from '@/apis/request.wms'

// 获取字典
export function queryCodeDictDetailApi(data, url = '/sys/wmsDict/find') {
  return request({
    url,
    method: 'post',
    data
  })
}

// 文件上传
export function uploadApi(url, params) {
  return requestWms({
    url,
    method: 'post',
    headers: {
      'Content-Type': 'multipart/form-data'
    },
    processData: false,
    data: params
  })
}

/*
*     文件上传和删除相关接口
*
*/

// 1. 文件删除
export function deleteUploadedFileCommonApi(data) {
  return requestWms({
    url: '/wms/file/del',
    method: 'post',
    data
  })
}

// 2. 根据ids 查找 文件列表
export function getUploadedFileListCommonApi(data) {
  return requestWms({
    url: '/wms/file/list',
    method: 'post',
    data
  })
}

// 获取机构下用户不分页（查询工作单元或者工作组）
export function getUserInGroupApi(url, data, module = 'lms') {
  if (module === 'wms') {
    return requestWms({
      url,
      method: 'post',
      data
    })
  } else {
    return request({
      url,
      method: 'post',
      data
    })
  }
}

