import Layout from '@/layout/Default'
function getBlankPath(path) {
  let origin = window.location.origin
  if (process.env.NODE_ENV === 'production') {
    origin = `${origin}${process.env.VUE_APP_BASE_PUBLIC_PATH}`
  }
  return `${origin}${path}`
}

export default {
  path: '/board',
  component: Layout,
  name: 'BoardModule',
  redirect: '/board/kanbanIndex',
  meta: {
    icon: '',
    title: '看板'
  },
  children: [
    {
      // path: 'kanbanIndex',
      path: getBlankPath('/board/kanbanIndex'),
      name: 'KanbanIndex',
      // component: () => import(/* webpackChunkName: "KanbanIndex" */ '@/views/board/kanbanIndex'),
      component: () => import(/* webpackChunkName: "KanbanIndex" */ '@/views/screen/analyticalOverviewRealV2'),
      meta: {
        title: '看板'
      },
      hidden: true
    },
    {
      path: 'kanbanConfig',
      name: 'KanbanConfig',
      component: () => import(/* webpackChunkName: "KanbanConfig" */'@/views/board/kanbanConfig'),
      meta: {
        icon: '',
        title: '看板配置'
      }
    },
    {
      path: 'addKanban',
      name: 'AddKanban',
      component: () => import(/* webpackChunkName: "AddKanban" */'@/views/board/kanbanConfig/addKanban'),
      meta: {
        icon: '',
        title: '新增看板'
      }
    },
    {
      path: 'editKanban/:id',
      name: 'EditKanban',
      component: () => import(/* webpackChunkName: "EditKanban" */'@/views/board/kanbanConfig/addKanban'),
      props: route => ({ id: route.params.id }),
      meta: {
        icon: '',
        title: '编辑看板'
      }
    },
    {
      path: 'previewKanban/:ids',
      name: 'PreviewKanban',
      component: () => import(/* webpackChunkName: "PreviewKanban" */'@/views/board/kanbanConfig/previewKanban'),
      props: route => ({ ids: route.params.ids }),
      meta: {
        icon: '',
        title: '预览看板'
      }
    },
    {
      path: 'setKanban',
      name: 'SetKanban',
      component: () => import(/* webpackChunkName: "SetKanban" */'@/views/board/kanbanConfig/setKanban'),
      meta: {
        icon: '',
        title: '看板设置'
      }
    }
  ]
}
