import Layout from '@/layout/Default'

export default {
  path: '/trace',
  component: Layout,
  name: 'TraceModule',
  redirect: '/trace/searchResult',
  meta: {
    icon: '',
    title: '追溯'
  },
  children: [
    {
      path: 'searchResult',
      name: 'SearchResult',
      component: () => import(/* webpackChunkName: "SearchResult" */'@/views/trace/searchResult'),
      meta: {
        icon: '',
        title: '查询结果'
      }
    },
    {
      path: 'relatedDocument/:barcode',
      name: 'RelatedDocument',
      component: () => import(/* webpackChunkName: "RelatedDocument" */'@/views/trace/document'),
      meta: {
        icon: '',
        title: '相关文档'
      }
    }
  ]
}
