/**
 * url验证
 * @param {string} path
 * @returns {Boolean}
 */
export function isExternal(path) {
  return /^(https?:|mailto:|tel:)/.test(path)
}
/**
 * 字符串验证
 * @param {string} str
 * @returns {Boolean}
 */
export function isString(str) {
  if (typeof str === 'string' || str instanceof String) {
    return true
  }
  return false
}

/**
 * 数组验证
 * @param {Array} arg
 * @returns {Boolean}
 */
export function isArray(arg) {
  if (typeof Array.isArray === 'undefined') {
    return Object.prototype.toString.call(arg) === '[object Array]'
  }
  return Array.isArray(arg)
}
// 比较运算
export function compare(a, b) {
  if (typeof a === 'number' && typeof b === 'number') {
    if (a < b) {
      return true
    }
  }
  return false
}
/**
 * 手机号验证
 * @param {string} poneInput
 * @returns {Boolean}
 */
export function isPoneAvailable(poneInput) {
  var myreg = /^[1][3,4,5,7,8][0-9]{9}$/
  if (!myreg.test(poneInput)) {
    return false
  } else {
    return true
  }
}
/**
 * 邮箱验证
 * @param {string} email
 * @returns {Boolean}
 */
export function validEmail(email) {
  const reg = /^(([^<>()\[\]\\.,;:\s@"]+(\.[^<>()\[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/
  return reg.test(email)
}
/**
 * 金钱验证  第一位不能为0，两位小数
 * @param {string} money
 * @returns {Boolean}
 */
export function validMoney(money) {
  const reg = /(^[1-9]([0-9]+)?(\.[0-9]{1,2})?$)|(^(0){1}$)|(^[0-9]\.[0-9]([0-9])?$)/
  return reg.test(money)
}
/**
 * element-ui 金钱验证  第一位不能为0，两位小数
 * @param {string} money
 * @returns {Boolean}
 */
export const validInputMoney = (rule, value, callback) => {
  if (!validMoney(value)) {
    callback(new Error('请输入正确的金额'))
  }
  callback()
}
/**
 * 中文姓名校验，支持少数民族
 * @param {string} name
 * @returns {Boolean}
 */
export function validChineseName(name) {
  const reg = /^[\u4E00-\u9FA5\uf900-\ufa2d·s]{2,20}$/
  return reg.test(name)
}
/**
 * 银行卡号严格校验
 * @param {string} str_cardNo
 * @returns {Boolean}
 */
export function validBankcard(str_cardNo) {
  str_cardNo = str_cardNo || String(this)
  if (str_cardNo.trim() === '' || undefined === str_cardNo) {
    return false
  }
  var lastNum = str_cardNo.substr(str_cardNo.length - 1, 1)// 取出最后一位（与luhm进行比较）

  var first15Num = str_cardNo.substr(0, str_cardNo.length - 1)// 前15或18位
  var newArr = []
  for (var i = first15Num.length - 1; i > -1; i--) { // 前15或18位倒序存进数组
    newArr.push(first15Num.substr(i, 1))
  }
  var arrJiShu = [] // 奇数位*2的积 <9
  var arrJiShu2 = [] // 奇数位*2的积 >9

  var arrOuShu = [] // 偶数位数组
  for (var j = 0; j < newArr.length; j++) {
    if ((j + 1) % 2 === 1) { // 奇数位
      if (parseInt(newArr[j]) * 2 < 9) { arrJiShu.push(parseInt(newArr[j]) * 2) } else { arrJiShu2.push(parseInt(newArr[j]) * 2) }
    } else {
      arrOuShu.push(newArr[j])
    }
  }

  var jishu_child1 = []// 奇数位*2 >9 的分割之后的数组个位数
  var jishu_child2 = []// 奇数位*2 >9 的分割之后的数组十位数
  for (var h = 0; h < arrJiShu2.length; h++) {
    jishu_child1.push(parseInt(arrJiShu2[h]) % 10)
    jishu_child2.push(parseInt(arrJiShu2[h]) / 10)
  }

  var sumJiShu = 0 // 奇数位*2 < 9 的数组之和
  var sumOuShu = 0 // 偶数位数组之和
  var sumJiShuChild1 = 0 // 奇数位*2 >9 的分割之后的数组个位数之和
  var sumJiShuChild2 = 0 // 奇数位*2 >9 的分割之后的数组十位数之和
  var sumTotal = 0
  for (var m = 0; m < arrJiShu.length; m++) {
    sumJiShu = sumJiShu + parseInt(arrJiShu[m])
  }

  for (var n = 0; n < arrOuShu.length; n++) {
    sumOuShu = sumOuShu + parseInt(arrOuShu[n])
  }

  for (var p = 0; p < jishu_child1.length; p++) {
    sumJiShuChild1 = sumJiShuChild1 + parseInt(jishu_child1[p])
    sumJiShuChild2 = sumJiShuChild2 + parseInt(jishu_child2[p])
  }
  // 计算总和
  sumTotal = parseInt(sumJiShu) + parseInt(sumOuShu) + parseInt(sumJiShuChild1) + parseInt(sumJiShuChild2)

  // 计算Luhm值
  var k = parseInt(sumTotal) % 10 === 0 ? 10 : parseInt(sumTotal) % 10
  var luhm = 10 - k

  if (lastNum === luhm.toString()) {
    return true
  } else {
    return false
  }
}

/**
 * IP校验
 * @param {string} ip
 * @returns {Boolean}
 */
export function validIP(ip) {
  var reg = /^(\d{1,2}|1\d\d|2[0-4]\d|25[0-5])\.(\d{1,2}|1\d\d|2[0-4]\d|25[0-5])\.(\d{1,2}|1\d\d|2[0-4]\d|25[0-5])\.(\d{1,2}|1\d\d|2[0-4]\d|25[0-5])$/
  return reg.test(ip)
}
