export function setUserInfo(info) {
  for (const key in info) {
    sessionStorage.setItem(`Admin${key}`, JSON.stringify(info[key]))
  }
  return
}

export function cookieToToken() {
  if (!getToken()) {
    const cookieToken = Cookie.get(TOKEN_KEY)
    if (cookieToken) {
      $store.commit('SET_TOKEN', cookieToken)
      // Cookie.remove(TOKEN_KEY)
    }
  }
}

export function getUserInfo(key) {
  if (sessionStorage.getItem(`Admin${key}`)) {
    return JSON.parse(sessionStorage.getItem(`Admin${key}`))
  } else if (key === 'userInfo') {
    return {
      loginName: '',
      userName: '',
      email: ''
    }
  } else {
    return []
  }
}

export function removeUserInfo(key) {
  if (key) {
    return sessionStorage.removeItem(`Admin${key}`)
  } else {
    return sessionStorage.clear()
  }
}

export function getLoginPath() {
  const baseUrl = Settings.fontBaseUrl[process.env.NODE_ENV]['board']
  return `${baseUrl}/login?project=${Settings.projectName}`
}

export function goBaseBoard() {
  const baseUrl = Settings.fontBaseUrl[process.env.NODE_ENV]['board']
  return `${baseUrl}?project=${Settings.projectName}`
}

export function storePermission(role) {
  $store.commit('SET_ROLES', role)
}

// 新版本 根据服务端返回的菜单树生成路由
export function serverMapRoutes(point, map) {
  const routes = []
  for (var i = 0; i < point.length; i++) {
    if (point[i].ident && map[point[i].ident]) {
      const item = map[point[i].ident]
      // meta标签
      // item.meta.title = point[i].name ? point[i].name : ''
      item.meta.resoureType = point[i].resoureType ? point[i].resoureType : 'url'
      item.meta.icon = point[i].icon ? point[i].icon : item.meta.icon
      item.hidden = point[i].isShow ? !point[i].isShow : true
      item.id = point[i].id ? point[i].id : ''
      item.path = point[i].isFrame ? point[i].href : item.path
      if (point[i].children && point[i].children.length > 0) {
        // resoure_type button 资源  url 资源
        const buttonSoure = point[i].children.filter(i => i.resoureType === 'btn')
        const roles = buttonSoure.map(item => {
          return item.permission
        })
        roles && roles instanceof Array && roles.length > 0 && storePermission(roles)
        const urlSoure = point[i].children.filter(i => i.resoureType === 'url')
        item.children = serverMapRoutes(urlSoure, map)
        item.meta.buttonSoure = buttonSoure
      }
      routes.push(item)
    }
  }
  return routes
}

// 扁平化 asyncRoutes 嵌套路由， 生成路由map映射对象  { name: route }
export function routesReduce(asyncRoutes) {
  let routesJson = asyncRoutes.reduce((prev, next) => {
    prev[next.name] = next
    return prev
  }, {})
  for (const key in routesJson) {
    if (routesJson[key].children && routesJson[key].children.length > 0) {
      const itemJson = routesReduce(routesJson[key].children)
      routesJson = { ...routesJson, ...itemJson }
    }
    delete routesJson[key].children
  }
  return routesJson
}
