import Layout from '@/layout/Default'

export default {
  path: '/analysis',
  component: Layout,
  name: 'AnalysisModule',
  redirect: '/analysis/overall',
  meta: {
    icon: 'chart',
    title: '统计分析'
  },
  children: [
    {
      path: 'overall',
      name: 'AnalysisOverall',
      component: () => import(/* webpackChunkName: "AnalysisOverall" */'@/views/analysis/overall'),
      meta: {
        icon: 'codeConsole',
        title: '概览'
      }
    },
    // 实时库存分析
    {
      path: 'inventory',
      name: 'AnalysisInventory',
      component: () => import(/* webpackChunkName: "AnalysisInventory" */'@/views/analysis/inventory'),
      meta: {
        icon: '',
        title: '实时库存分析'
      }
    },
    {
      path: 'inventory-detail/:id',
      name: 'AnalysisInventoryDetail',
      component: () => import(/* webpackChunkName: "AnalysisInventoryDetail" */'@/views/analysis/inventory/detail'),
      meta: {
        icon: 'database',
        title: '实时库存分析详情'
      }
    },
    // 出入库单据查询
    {
      path: 'out-inbound-order',
      name: 'AnalysisoutInbOundOrder',
      component: () => import(/* webpackChunkName: "AnalysisoutInbOundOrder" */'@/views/analysis/outInboundOrder'),
      meta: {
        icon: '',
        title: '出入库单据查询'
      }
    },
    {
      path: 'material-in-transit',
      name: 'AnalysisMaterialInTransit',
      component: () => import(/* webpackChunkName: "AnalysisMaterialInTransit" */'@/views/analysis/materialInTransit'),
      meta: {
        icon: '',
        title: '在途物资统计分析'
      }
    },
    {
      path: 'material-price',
      name: 'AnalysisMaterialPrice',
      component: () => import(/* webpackChunkName: "AnalysisMaterialPrice" */'@/views/analysis/materialPrice'),
      meta: {
        icon: '',
        title: '物料价格分析'
      }
    },
    {
      path: 'order-contract',
      name: 'AnalysisOrderContract',
      component: () => import(/* webpackChunkName: "AnalysisOrderContract" */'@/views/analysis/orderContract'),
      meta: {
        icon: '',
        title: '订单合同统计分析'
      }
    },
    {
      path: 'order-contract-detail/:id',
      name: 'AnalysisOrderContractDetail',
      component: () => import(/* webpackChunkName: "AnalysisOrderContractDetail" */'@/views/analysis/orderContract/detail'),
      meta: {
        icon: 'database',
        title: '订单合同统计分析详情'
      }
    },
    {
      path: 'supplier',
      name: 'AnalysisSupplier',
      component: () => import(/* webpackChunkName: "AnalysisSupplier" */'@/views/analysis/supplier'),
      meta: {
        icon: '',
        title: '供应商综合分析'
      }
    },
    {
      path: 'supplier-detail/:id',
      name: 'AnalysisSupplierDetail',
      component: () => import(/* webpackChunkName: "AnalysisSupplierDetail" */'@/views/analysis/supplier/detail'),
      meta: {
        icon: 'database',
        title: '供应商详情'
      }
    },
    {
      path: 'warehouse-utilization-rate',
      name: 'AnalysisWarehouseUtilizationRate',
      component: () => import(/* webpackChunkName: "AnalysisWarehouseUtilizationRate" */'@/views/analysis/warehouseUtilizationRate'),
      meta: {
        icon: '',
        title: '库位利用率分析'
      }
    },
    {
      path: 'checkWarehouseResult',
      name: 'AnalysisCheckWarehouseResult',
      component: () => import(/* webpackChunkName: "AnalysisCheckWarehouseResult" */'@/views/analysis/checkWarehouseResult'),
      meta: {
        icon: '',
        title: '盘点结存单'
      }
    }
  ]
}
